<script>
import { onMounted, reactive, toRefs, getCurrentInstance, watch } from "vue";
// import addico from '../assets/sections/expand.svg'
// import delico from '../assets/sections/contract.svg'
export default {
  name: "Home",
  setup() {
    const state = reactive({
      connect: {
        isConnect: false,
        address: "",
        balance: "",
      },
      totalSupply: 10000,
      mintTotalSupply: 0,
      currentContract: null,
      mintPrice: 10,
      mintNumber:1,
      amount:10,
      showModel:{
        tip1:false,
        tip2:false,
        tip3:false,
        tip4:false,
        tip5:false,
        tip6:false,
      },
      activeIndex:0,
    });
    const { proxy } = getCurrentInstance();

    state.connect = proxy.$store.state.connect;
    const connectWallet = async function () {
      const connectResult = await proxy.$utils.CONNECT.linkWallet();
      state.connect.isConnect = connectResult.state;
      if (connectResult.state) {
        state.connect.address = connectResult.address;
        state.connect.balance = connectResult.balance;
        proxy.$store.commit("changeconnect", {
          isConnect: true,
          address: connectResult.address,
          balance: connectResult.balance,
        });
      } else {
        alert(connectResult.msg);
      }
    };
    const disconnect = function () {
      state.connect = {
        isConnect: false,
        address: "",
        balance: "",
      };
      proxy.$store.commit("changeconnect", {
        isConnect: false,
        address: "",
        balance: "",
      });
    };
    const calculation = function(t){
      if(t==1&&state.mintNumber>0){
        state.mintNumber=state.mintNumber-1;
      }else if(t==2){
        state.mintNumber=state.mintNumber+1;
      }
      if(state.mintNumber==''){
        state.amount=0
      }else{
        state.amount = state.mintNumber*state.mintPrice;
      }
      
    }
    //获取已经铸造数量
    const getTotalSupply = async function () {
      let currentContract = proxy.$utils.CONTRACT.getCurrentContract();
      if (currentContract != null) {
        state.currentContract = currentContract;
        state.mintTotalSupply = await proxy.$utils.CONTRACT.totalSupply();
      }
    };
    const getMint = async function () {
      if (state.mintNumber == "" || state.mintNumber == 0) {
        alert("Please enter the correct casting quantity");
        return false;
      }
      if (state.mintNumber > 200) {
        alert("The maximum casting quantity is 200");
        return false;
      }
      if (state.currentContract == null) {
        state.currentContract = proxy.$utils.CONTRACT.getCurrentContract();
      }
      if (state.currentContract != null) {
        let balance = await proxy.$utils.CONNECT.getbalance(state.connect.address);
        if (Number(balance) < state.amount) {
          alert("Insufficient wallet balance");
          return false;
        }
        proxy.$utils.CONTRACT.publicMint(
          state.mintNumber,
          state.mintPrice,
          function (res) {
            alert("Casting success");
            state.mintNumber = "";
            setTimeout(async function () {
              getTotalSupply();
            }, 1000);
          },
          function (err) {
            alert("Casting failed. Please recast later");
          }
        );
      }
    };
    const getlocal = function(selectId){
      //找到锚点id
      let toElement = document.getElementById(selectId);
      //如果对应id存在，就跳转
      if(selectId!=""){
          toElement.scrollIntoView()
      }
    };
    const checkLink = function(){
      if(state.connect.isConnect){
        // getlocal('JoinTheAcrocalypse')
        window.open("https://testnet.apenft.io/collections/tronacrocalypse")
      }else{
        connectWallet();
      }
    }
    onMounted(() => {});
    return {
      ...toRefs(state), // 将 state 返回出去，就可以直接使用 state 里面的属性
      connectWallet,
      disconnect,
      getTotalSupply,
      getMint,
      getlocal,
      checkLink,
      calculation,
    };
  },
};
</script>
  


